import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";

import { DiscourseGrid } from "./components/compounds/discoursegrid";
import { NameTable } from "./components/compounds/nametable";
import { namedDiscourses, discourseNames } from "./discourselist.json";
import { InfoBox } from "./components/elements/infobox/infobox";
import "./App.css";

const App = () => {
  const cookies = new Cookies();
  const [displayState, setDisplayState] = useState("nodisplay");
  const [tableOpen, setTableOpen] = useState(false);
  const [infoBoxState, setInfoBoxState] = useState(false);

  let namesCookie = cookies.get("discourseNames");
  let discourseCookie = cookies.get("namedDiscourses");

  useEffect(() => {
    if (!namesCookie)
      cookies.set("discourseNames", JSON.stringify(discourseNames), {
        path: "/",
      });
    if (!discourseCookie)
      cookies.set("namedDiscourses", JSON.stringify(namedDiscourses), {
        path: "/",
      });
  }, []);

  const isItTime = () => {
    let time = new Date();
    var hours = time.getHours();
    var minutes = time.getMinutes();
    if (hours === 4 && minutes === 20) setDisplayState("display");
    else setDisplayState("nodisplay");
  };

  useEffect(() => {
    let interval = setInterval(() => isItTime(), 1000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  const handleCloseTable = () => {
    setTableOpen(false);
  };

  return (
    <div className="main-container">
      <div className={`blaze-${displayState}`}><p>420</p><p>BLAZE IT!!!</p></div>
      <DiscourseGrid
        setTableOpen={setTableOpen}
        namedDiscourses={discourseCookie}
        discourseNames={namesCookie}
      />

      {tableOpen ? (
        <NameTable
          handleCloseTable={handleCloseTable}
          namedDiscourses={discourseCookie}
          discourseNames={namesCookie}
        />
      ) : null}
      <a href="#" className="info-toggle" onClick={() => setInfoBoxState(prevstate => !prevstate)}>
        [v1.0]
      </a>
      {infoBoxState ? (
        <InfoBox
          infoBoxState={infoBoxState}
          setInfoBoxState={setInfoBoxState}
        />
      ) : null}
    </div>
  );
};

export default App;
